import React, { Component } from 'react';
import { Collapse } from 'antd';

import appStyle from './question.module.css'
import './question.css'
// console.log(appStyle)
import Axios from 'axios'
import Http from '../../http/http'
class Head extends Component {
    // constructor(props){
    //     super(props)
    //     this.state = {
    //         questions: []
    //     }
    // }
    state = {
        questions: []
    }
    componentDidMount(ev) {
        this.getQuestionList()
    }
    getQuestionList() {
        let _this = this;
        Axios.get(Http.questionList)
            .then(res => {
                let v = res.data;
                // console.log(v, '问题列表')
                if (v && v.data.length) {
                    _this.setState({
                        questions: v.data
                    })
                }
            })
    }
    render() {
        return (
            <div className={appStyle.questions}>
                <img className={appStyle.banner} src={require('../../assets/images/bg_problems@3x.png')} alt="" />
                <div className={appStyle.main + " question-main"}>
                    <Collapse
                        bordered={false}
                        accordion
                    // defaultActiveKey={['1']}
                    >
                        {
                            this.state.questions && this.state.questions.map((item,i)=>(
                                <Collapse.Panel header={"・" + item.title } key={ item.id }>{ item.answer }</Collapse.Panel>
                            ))
                        }
                        
                        {/* <Collapse.Panel header="・This is panel header 2" key="2">
                            panel2
                        </Collapse.Panel>
                        <Collapse.Panel header="・This is panel header 3" key="3">
                            panel3
                        </Collapse.Panel> */}
                    </Collapse>
                </div>
            </div>
        );
    }
}

export default Head;