function map(el) {
    var map = new BMap.Map(el); // 创建Map实例
    var c = new BMap.Point(116.647076,39.911812)
    map.setCurrentCity("北京"); // 设置地图显示的城市 此项是必须设置的
    map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
    map.centerAndZoom(c, 15);  // 初始化地图,设置中心点坐标和地图级别
    var opts = {    
        width : 250,     // 信息窗口宽度    
        height:40,     // 信息窗口高度    
        title : "中盛普惠"  // 信息窗口标题   
    }    
    var infoWindow = new BMap.InfoWindow("北京市通州区新华西街58号院3号楼14层", opts);  // 创建信息窗口对象    
    map.openInfoWindow(infoWindow, map.getCenter());      // 打开信息窗口
}

