function convertData(data) {
    var res = [];
    for (var i = 0; i < data.length; i++) {
        var geoCoord = geoCoordMap[data[i]];
        if (geoCoord) {
            res.push({
                name: data[i],
                // value: geoCoord.concat(data[i].value)
                value: geoCoord
            });
        }
    }
    return res;
}
