  productMove(val) {   // 产品列表展示 箭头控制移动
    // console.log(val)
    // console.log("总长度",this.state.productWidth,"移动长度",this.state.productTransLen,val)
    let _this = this;
    let len = val * (1000 + 13) + this.state.productTransLen;
    // console.log(len,len < (-1 * this.state.productWidth + 1000))
    if ((len < (-1 * this.state.productWidth + 1000)) || len > 0) {
      // console.log(len,1111)
      this.setState({
        productTransLen: len > 0 ? 13 : (-1 * this.state.productWidth + 1000 - 13)
      }, ev => {
        setTimeout(() => {
          _this.setState({
            productTransLen: len > 0 ? 0 : (-1 * this.state.productWidth + 1000)
          })
        }, 300)
      })
    } else {
      this.setState({
        productTransLen: len
      })
    }
  }
  caculate() {     // 数据计算方法
    let _this = this;
    let m = this.state.caculateParams;
    const y = {
      marriage: 0,
      educate: 0,
      house: 0,
      annualIncome: 0,
      credit: 0,
      phone: ''
    }
    switch (this.state.caculateState) {
      case 0:
        _this.setState({
          caculateState: 1
        }, ev => {
          setTimeout(() => {
            _this.setState({
              caculateCoverPicShow: false
            })
          }, 300)
        })
        break;
      case 1:
        // _this.caculateResult()
        if (!m.phone || !(/^1[3-9]\d{9}$/.test(m.phone))) {
          message.error("请输入正确的手机号")
          return;
        }
        if (!m.marriage) {
          // console.log(m,m.marriage,222222222)
          message.error("请选择婚姻")
          return;
        }
        if (!m.educate) {
          message.error("请选择学历")
          return;
        }
        if (!m.house) {
          message.error("请选择居住")
          return;
        }
        if (!m.annualIncome) {
          message.error("请选择年收入")
          return;
        }
        if (!m.credit) {
          message.error("请选择信用")
          return;
        }
        _this.caculateResult()
        break;
      case 3:
        _this.setState({
          caculateState: 0,
          caculateParams: y
        })
        break;
    }
  }
  changeInfo(val, option) {   // 填写计算信息
    // console.log( val,option ,2222222222)
    let m = this.state.caculateParams;
    console.log(val, option, m)
    if (option === 'phone') {
      m[option] = val.target.value
    } else {
      m[option] = val * 1
    }
    this.setState({
      caculateParams: m
    })
  }
  caculateResult() {  // 计算输出
    const _this = this;
    let m = this.state.caculateParams;
    let v = 0;
    let r = '';
    let c = {
      marriage: [20, 5],
      educate: [15, 75],
      house: [45, 15],
      annualIncome: [10, 35, 70],
      credit: [50, 25, 0]
    }
    Axios.get(Http.caculateR, {
      params: {
        marriage: m.marriage,
        education: m.educate,
        dwell: m.house,
        income: m.annualIncome,
        credit: m.credit,
        phone: m.phone
      }
    })
      .then(res => {
        console.log(res)
      })
    for (let i in m) {
      // console.log(c[i],m[i],c[i][m[i]])
      if (c[i] && c[i][m[i] - 1]) {
        // console.log(i,c[i][m[i]-1])
        v += c[i][m[i] - 1]
      }
    }
    // console.log(v, 1111111111111, m)
    if (v > 240) {
      r = "可获批最高额度"
    } else if (v > 180 && v <= 240) {
      r = "可获批高额度"
    } else if (v > 120 && v <= 180) {
      r = "可获批较高额度"
    } else if (v > 60 && v <= 120) {
      r = "可获批额度"
    } else if (v > 0 && v <= 60) {
      r = "可以申请"
    }
    this.setState({
      caculateResult: r,
      caculateState: 2
    }, ev => {
      setTimeout(() => {
        _this.setState({
          caculateState: 3
        })
      }, 2000)
    })
  }
