import React, { Component } from 'react';
import { NavLink, Link, withRouter } from 'react-router-dom'
import { BackTop } from 'antd'
import appStyle from './head.module.css';
import logo from '../../assets/images/logo_normal@3x.png'
import logoTransparent from '../../assets/images/top_navbar_logo@2x.png'
@withRouter
class Head extends Component {
  // constructor(){
  //   super();
  //   // this.scrollEvent = this.scrollEvent.bind(this)
  //   this.state = {
  //     top:false,
  //     showcon:true
  //   }
  // }
  state = {
    top:false,
    showcon:true
  }
  componentDidUpdate(){
    // console.log(111)
  }
  componentDidMount() {
    this.props.location.pathname === '/' && window.addEventListener('scroll', this.scrollEvent.bind(this) );
  }
  scrollEvent(ev){
    let HT = ev.srcElement;
    var top = HT.documentElement.scrollTop || HT.body.scrollTop;
    // console.log(top,this.state)
    if(this.props.location.pathname==='/' && top!==0){
      
    }

    if( this.state && ((!!top) !== this.state.top) ){
      this.setState({
        top: (!!top)
      })
    }
  }
  componentDidUpdate(){
    // console.log(this.props.location.pathname)
    this.props.location.pathname === '/' && window.addEventListener('scroll', this.scrollEvent.bind(this) );
  }
  componentWillUnmount(){
    window.removeEventListener("scroll",this.scrollEvent)
  }
  showstring(){
    this.setState({
      showcon:false
    })
  }
  industry(){
    this.setState({
      showcon:true
    })
  }
  render() {
    return (
      <div className={ (this.props.location.pathname === '/' && !this.state.top) ? "zsqy-index-header header-bj-home" : "zsqy-index-header header-bj" }>
        <div className="header-content clearfix">
          <Link className="logo fl" to="/">
            <img src={ (this.props.location.pathname === '/' && !this.state.top) ? logoTransparent : logo} alt="中盛普惠" />
          </Link>
          <div className={ (this.props.location.pathname === '/' && !this.state.top) ? "header-nav-home fr" : "header-nav fr"}>
          
            <Link className={ (this.props.location.pathname === '/' && this.state.top) ? "active" :'' } to="/">首页</Link>
            {/* <NavLink activeClassName="active" to="/products">产品中心</NavLink> */}
            {/* <NavLink activeClassName="active" to={{pathname:'/news',search:"?index=1"}}>行业资讯</NavLink> */}
            <a href="javascript:void(0);" onClick={e=>this.showstring()}>行业资讯</a>
            {/* <NavLink activeClassName="active" to="/m">移动端APP</NavLink> */}
            <NavLink activeClassName="active" to="/about">关于我们</NavLink>
          </div>
        </div>
          <BackTop />
          <div className={(this.state.showcon==false)?appStyle.aLLcontentIMG:appStyle.aLLcontentIMGS} onClick={e=>this.industry()}>
            <div className={appStyle.BodyIMG}><img src={require("../../assets/images/bg_news_dropdown@2x.png")}></img></div>
          </div> 
      </div>
    );
  }
}

export default Head;