import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom'
import appStyle from './news.module.css';
import Topimg from '../../assets/images/bg_news@3x.jpg';
import { Pagination } from 'antd';
import Axios from 'axios';
import Http from '../../http/http';
import { formatDate } from '../../tools/tools';

@withRouter
class News extends Component {
//   constructor(props){
//     super(props)
//     this.state = {
//       nowTab: '',
//       Reslist: [],
//       listsize: '',
//       page: 1,            // 页码
//       type: 1,            // tab 的类型 新闻
//       contentTitle: [
//         { names: '行业资讯', id: 1 },
//         { names: '行业动态', id: 2 },
//         { names: '监管政策', id: 3 },
//         { names: '最新活动', id: 4 },
//       ],
//       // numberList:1,
//     }
// }
state = {
  nowTab: '',
  Reslist: [],
  listsize: '',
  page: 1,            // 页码
  type: 1,            // tab 的类型 新闻
  contentTitle: [
    { names: '行业资讯', id: 1 },
    { names: '行业动态', id: 2 },
    { names: '监管政策', id: 3 },
    { names: '最新活动', id: 4 },
  ],
  // numberList:1,
}
  componentWillReceiveProps(ev) {
    // console.log(ev)
    if (ev.location.search.split('?index=')[1] !== 'undefined') {
      // this.state.type=decodeURI(this.props.location.search.split('?index=')[1])
      let c = Number(ev.location.search.split('?index=')[1]);
      this.setState({
        type: c
      }, () => this.functionClick(1, c))
    }
  }
  functionClick(page, type) {   // 获取数据列表
    var fd = new FormData();
    let that = this
    // console.log(that.state.type)
    Axios.get(Http.newsList + '?type=' + (type ? type : that.state.type) + '&page=' + (page ? page : that.state.page), fd, {
      //   params: {
      //     page:page ? page : that.state.page
      // }
    }).then(function (response) {
      // console.log(response, '3333')
      var ResPose = response.data
      if (ResPose && ResPose.data && ResPose.data.data.length) {
        ResPose.data.data.forEach((item) => {
          item.create_time = formatDate(item.create_time * 1000, "ymd")
        })
        that.setState({
          Reslist: ResPose.data.data,
          listsize: ResPose.data.pageCount * ResPose.data.limit,
          pageSize: ResPose.data.limit * 1,
        })
      } else {
        that.setState({
          Reslist: ResPose.data.data,
          listsize: ResPose.data.pageCount * ResPose.data.limit,
          pageSize: ResPose.data.limit * 1,
        })
      }
    }).catch(function (error) {

    });
  }
  corresponding(value) {       // changetab 时，调取数据
    // console.log('2222')
    var valuescontent = 1 + value
    var locations = this.props.location
    this.props.history.push(locations.pathname + '?index=' + valuescontent)
    // this.state.type=value+1;
    // this.state.page=1
    this.setState({
      type: 1 + value,
      page: 1,
      // numberList:value
    }, () => this.functionClick(1, 1 + value))
  }
  componentDidMount() {
    // console.log(decodeURI(this.props.location.search.split('?index=')[1]))
    if (decodeURI(this.props.location.search.split('?index=')[1]) !== 'undefined') {
      // this.state.type=decodeURI(this.props.location.search.split('?index=')[1])
      let c = Number(decodeURI(this.props.location.search.split('?index=')[1]));

      this.setState({
        type: c
      }, () => this.functionClick(1, c))
    } else {
      // console.log(window.location)
      // this.props.history.push(window.location.href + '?index=' + 1)
      this.functionClick()
    }
  }
  componentDidUpdate(){
    // console.log(111111111111)
  }
  onChange(page, pageSize) {   // 分页
    // this.state.page=page
    // console.log(page)
    this.setState({
      page: page
    }, () => {
      this.functionClick(page)
      // console.log(this.state.page)
    })
  }
  render() {
    return (
      <div className={appStyle.news}>
      {/* 点击进入页面禁止访问页面的内容 */}
      {/* <div className={appStyle.aLLcontentIMGS}>
        <div className={appStyle.BodyIMG}><img src={require("../../assets/images/bg_news_dropdown@2x.png")}></img></div>
      </div> */}
        <div style={{
          background: `url('${Topimg}') center top / cover no-repeat`,
          height: 300
        }}>
        </div>   
        {/* 点击事件 */}
        <div className={appStyle.newsclick}>
          <ul>
            {
              this.state.contentTitle.map((item, index) => {
                return <li className={(item.id === this.state.type) ? appStyle.us_color : appStyle.About_Us} key={index} onClick={e => this.corresponding(index)}>{item.names}</li>
              })
            }
          </ul>
          <div className={appStyle.Bottom_list}>
            {/* 1 */}
            {
              <div className={appStyle.BottomListcont}>
                {/* 内容 */}
                {
                  this.state.Reslist.map((item, id) => {
                    return (
                      <div className={appStyle.intobei} key={item.id}>
                        <img src={item.journalism_image} alt=""></img>
                        <div className={appStyle.Contenttext}>
                          <p><Link to={"/newlist?news=" + item.id} className={appStyle.TItletext}>{item.journalism_title}</Link></p>
                          <div className={appStyle.ENtertime}>{item.journalism_intro}</div>
                          <p>{item.create_time}</p>
                        </div>
                      </div>
                    )
                  })
                }
                <div className={appStyle.Paing}>
                  <Pagination
                    current={this.state.page}
                    pageSize={this.state.pageSize}
                    // defaultCurrent={1} 
                    total={this.state.listsize}
                    showQuickJumper={true}
                    onChange={(page, pageSize) => this.onChange(page, pageSize)}
                  />
                </div>
              </div>
            }
          </div>
        </div>
      </div>
    );
  }
}

export default News;