
import '@babel/polyfill'
import 'es5-shim'
import 'es5-shim/es5-sham'
import 'es5-sham-ie8'
import 'console-polyfill'
import 'fetch-ie8'
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Redirect, Switch, withRouter } from 'react-router-dom'

import './index.css';
import Index from './route/home/home';
import About from './route/aboutus/about';
import Mobile from './route/m/m';
import News from './route/news/news';
import Products from './route/products/product';
import ProductDetail from './route/productDetail/productDetail';
import Questions from './route/questions/question';
import Foot from './components/foot/foot';
import Head from './components/head/head';
import Newslist from './route/newlist/newlist';



@withRouter
class App extends Component {
    render() {
        return (
            <div className="main"
                style={{
                    paddingTop: this.props.location.pathname === '/' ? 0 : 60, overflowX: "hidden"
                }}>
                <Head />
                <Switch>
                    <Route exact path="/" component={Index} />
                    <Route exact path="/news" component={News} />
                    <Route exact path="/about" component={About} />
                    <Route exact path="/m" component={Mobile} />

                    <Route exact path="/products" component={Products} />
                    <Route exact path="/products/:class" component={ ProductDetail } />
                    
                    <Route exact path="/questions" component={Questions} />
                    <Route exact path="/newlist" component={Newslist} />

                    <Redirect from="*" to="/" />
                </Switch>
                <Foot />
            </div>
        )
    }
}

ReactDOM.render(
    <BrowserRouter>
        <App />
    </BrowserRouter>
    , document.getElementById('root'));