import React, { Component } from 'react';
import appStyle from './productDetail.module.css';
import Axios from 'axios'
import Http from '../../http/http'
import { message } from 'antd';

// banner 图片引入
const picObj = {
  online: require('../../assets/images/bg_online@3x.png'),
  elite: require('../../assets/images/bg_elite@3x.png'),
  agriculture: require('../../assets/images/bg_agriculture@3x.png'),
  business: require('../../assets/images/bg_business@3x.png'),
  elitewomen: require('../../assets/images/bg_elitewomen@3x.png'),
  worker: require('../../assets/images/bg_worker@3x.png'),
  tech: require('../../assets/images/bg_tech@3x.png'),
  5060: require('../../assets/images/bg_5060@3x.png'),
  insurance: require('../../assets/images/bg_insurance@3x.png'),
  arrowD: require('../../assets/images/to_bottom.png'),    // 立即申请的箭头
  arrowUp: require('../../assets/images/to_top.png')
}


// console.log(appStyle)
class Product extends Component {
//   constructor(props){
//     super(props)
//     this.state = {
//       banner: '',   // banner 图的类型
//       arrow: picObj.arrowD,    // 立即申请的箭头
//       showDownLoad: false,    // 是否显示下载框
//       wechatCode:'',
//       appCode:'',
//       products:[],
//       pid:'',
//       moneyPM:0,          // 每月应还
//       moneyAmount:0,      // 总计应还
//       askMoney:0,  // 申请金额
//       month:12,            // 还款月数
//     }
// }
state = {
  banner: '',   // banner 图的类型
  arrow: picObj.arrowD,    // 立即申请的箭头
  showDownLoad: false,    // 是否显示下载框
  wechatCode:'',
  appCode:'',
  products:[],
  pid:'',
  moneyPM:0,          // 每月应还
  moneyAmount:0,      // 总计应还
  askMoney:0,  // 申请金额
  month:12,            // 还款月数
}
  componentDidMount() {
    //跳转页面回到顶部
    document.documentElement.scrollTop = 0
    let _this = this;
    // console.log(this.props.match.params)
    this.getCode();
    let banner = this.props.match.params.class
    // this.setState({
    //   banner: picObj[banner]
    // })
    this.setState({
      // banner: picObj["online"],
      banner:this.props.match.params.class,
      pid:banner
    })
    let products = sessionStorage.getItem("products")
    if( products ) {
      products = JSON.parse(products);
      products.length && _this.setState({
        products : products
      })
    }else{
      _this.getProductList()
    }
  }
  getCode() {  // 获取二维码接口
    Axios.get(Http.code)
      .then(res => {
        // console.log(res, "二维码")
        let v = res.data;
        if (v && v.data) {
          // console.log(v.data.imges1)
          this.setState({
            wechatCode: v.data.imges1,
            appCode: v.data.imges2
          })
        }
      })
  }
  showDownLoad(val) {
    this.setState({
      arrow: picObj[val],
      showDownLoad: !this.state.showDownLoad
    })
  }
  getProductList() {  // 产品列表
    let _this = this;
    Axios.get(Http.productList)
      .then(res => {
        // console.log('1111')
  console.log(res)
        let v = res.data;
        // console.log(v, '产品列表')
        if (v && v.data && v.data.length) {
          sessionStorage.setItem("products",JSON.stringify(v.data))
          _this.setState({
            // productWidth: 253 * v.data.length,
            products: v.data
          })
        }
      })
  }
  changeMonth(val){  // 更改月份
    this.setState({
      month:val*1
    })
  }
  caculate(){   // 计算结果获取
    let _this = this;
    if(this.state.askMoney){
      Axios.get(Http.caculateMoney,{
        params: {
          check_money:this.state.askMoney,
          deadline: this.state.month
        }
      })
        .then(res => {
          let v = res.data;
          // console.log(v, '计算结果获取')
          if(v && v.data){
            _this.setState({
              moneyPM:(Math.ceil(v.data.month * 100)/100).toFixed(2),
              moneyAmount:( Math.ceil(v.data.sum * 100) / 100).toFixed(2)
            })
          }
        })
    }else{
      message.error("请输入金额")
    } 
  }
  changeAskMoney(ev){  // 修改申请金额
    this.setState({
      askMoney:ev.target.value * 1
    })
  }
  render() {
    let v;
    // console.log(this.state.products)
    // console.log(this.state.pid)
    if(this.state.products && this.state.pid ) v = this.state.products[this.state.pid];
    return (
      <div className={appStyle["products"]}>
          {/* 回到顶部 */}
        <div className={appStyle.banner} >
          {/* <img className={appStyle.banner} src={v&&v.bg_image} alt="产品中心" /> */}
          <div style={{
                    background: `url(${v&&v.bg_image}) center top / cover no-repeat`,
                    //  width:'100%',
                    height: 400
                }}></div>
          <div className={appStyle["lcy-banner-cont"]}>
            <div className={appStyle["lcy-details-type"] + " clearfix"}>
              <p className={appStyle["lcy-details-name"] + " fl"}>
                {
                  v && v.product_name
                }
              </p>
              <p className={appStyle["lcy-details-apply"] + " fr"}
                onMouseOver={ev => { this.showDownLoad("arrowUp") }}
                onMouseOut={ev => { this.showDownLoad("arrowD") }}
              >
                <span>立即申请</span>
                <img src={this.state.arrow} alt="立即申请" />
              </p>
              <div className={appStyle["down-icon-erwm"]}
                style={this.state.showDownLoad ? style.showDownLoad : { display: 'none' }}
              >
                <p>推荐您到微信公众号或下载APP继续申请借款服务<br />申请额度更高，撮合效率更快哟！</p>
                <div className={appStyle["ldDownloadApp-erw-img"]} >
                  <div className={appStyle["erw-img"]}>
                    <img src={ this.state.wechatCode } alt="中盛普惠微信公众号" />
                    <p>微信公众号</p>
                    <i></i>
                  </div>
                  <div className={appStyle["erw-img"]}>
                    <img src={ this.state.appCode } alt="中盛普惠APP下载" />
                    <p>下载APP</p>
                  </div>
                </div>
              </div>
            </div>
            {/* <!-- 下载弹框 --> */}
            <div className={appStyle["lcy-details-introduce"]}>
              <ul className="clearfix">
                <li>
                  <p>{ v && v.product_amount }</p>
                  <span>可申请额度</span>
                </li>
                {/* <!-- 当月数多的时候添加‘more’类名 --> */}
                <li>
                  <p className={appStyle["more"]}>{  v && v.product_deadline }</p>
                  <span>可申请期限</span>
                </li>
                <li>
                  <p>{  v && v.product_date }</p>
                  <span>撮合时间</span>
                </li>
                <li>
                  <p>{  v && v.product_repayment }</p>
                  <span>还款方式</span>
                </li>
              </ul>
            </div>
          </div>
        </div>
        {/* 申请要求 */}
        <div className={appStyle["lcy-claim"]}>
          <div className={appStyle["lcy-index-title"]}>
            <p className={appStyle["title"]}>申请要求</p>
            <p className={appStyle["xian"]}></p>
          </div>
          <div className={appStyle["lcy-claim-cont"] + " clearfix"}>
            {/* <ul className="fl">
              <li className="clearfix">
                <p>1、中国籍大陆公民，45-63周岁，具有完全民事行为能力；</p>
              </li>
              <li className="clearfix">
                <p>2、本人和配偶及其经营主体均无法院“执行中”的案件和任何民事及刑事纠纷；</p>
              </li>
              <li className="clearfix">
                <p>3、人民银行征信报告非空白；</p>
              </li>
              <li className="clearfix">
                <p>4、申请人须是经营主体的法定代表人或持股比例在20%及以上的股东；</p>
              </li>
              <li className="clearfix">
                <p>5、经营主体成立、实际经营时间和股权变更时间均在24个月以上；</p>
              </li>
              <li className="clearfix">
                <p>6、经营实体在适用地域工商管理部门注册登记且主要经营活动发生在该行政辖区内。</p>
              </li>
            </ul> */}
            {
            v && <div className="fl"  style={{paddingTop:'16px',paddingLeft:'16px',fontSize:'16px',lineHeight:'25px',width:'580px',color:'#C2955D'}}
              dangerouslySetInnerHTML={{__html:(v.product_require) }}
              // dangerouslySetInnerHTML={{__html: "<p>sdfsdfsfsd</p><h1>sdfsdf</h1>" }}
            ></div>
            }
            
            <div className="fr">
              <img src={require("../../assets/images/pic@3x.png")} alt="申请要求" />
            </div>
          </div>
        </div>
        {/* 费用计算 */}
        <div className={appStyle["lcy-cost"]}>
          <div className={appStyle["lcy-cost-cont"]}>
            <div className={appStyle["lcy-little-title"]}>
              <i></i>
              <span>借款费用试算</span>
            </div>
            <div className={appStyle["lcy-cost-result"]}>
              <div className={appStyle["lcy-should-repay"] + " clearfix"}>
                <p>每月应还：<span>{ this.state.moneyPM }</span></p>
                <p>总计应还：<span>{ this.state.moneyAmount }</span></p>
              </div>

              <div className={"clearfix " + appStyle["lcy-cost-parameter"]}>
                <div className={appStyle["lcy-money"] + " fl " + appStyle.shuru}>
                  {/* <input type="number" placeholder="请输入申请金额" value={ this.state.askMoney?this.state.askMoney:'' } onChange={ ev=>this.changeAskMoney(ev) } /> */}
                  <input type="text" placeholder="请输入申请金额" value={ this.state.askMoney?this.state.askMoney:'' } onChange={ ev=>this.changeAskMoney(ev) } />
                </div>
                <div className={" fl  clearfix " + appStyle["lcy-duration"]}>
                  <span onClick={ ev=>{ this.changeMonth(6) } } className={ this.state.month=== 6 && appStyle.on }>6个月</span>
                  <span onClick={ ev=>{ this.changeMonth(12) } } className={ this.state.month=== 12 && appStyle.on }>12个月</span>
                  <span onClick={ ev=>{ this.changeMonth(24) } } className={ this.state.month=== 24 && appStyle.on }>24个月</span>
                </div>
                <div onClick={ ev=>{ this.caculate() } } className={appStyle["lcy-determine"] + " fl " + appStyle.shuru}>计算</div>
                {/* <div className={appStyle["lcy-determine"] + " fl " + appStyle.shuru} style={{background:'#ccc'}}>计算</div>  */}
              </div>
            </div>
          </div>
        </div>
        {/* 申请流程 */}
        <div className={appStyle["lcy-flow-diagram"]}>
          <div className={appStyle["lcy-little-title"]}>
            <i></i><span>申请流程</span>
          </div>
          <img src={require("../../assets/images/pic_procedure.png")} alt="申请流程" />
        </div>
      </div>
    );
  }
}

const style = {
  showDownLoad: {
    display: 'block'
  }
}
export default Product;