import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom'
import appStyle from './newlist.module.css';
import Topimg from '../../assets/images/bg_news@3x.jpg';
// import { Pagination } from 'antd';
import Axios from 'axios';
import Http from '../../http/http';
import { formatDate } from '../../tools/tools';

@withRouter
class News extends Component {
//   constructor(props){
//     super(props)
//     this.state = {
//       nowTab: '',
//       listitems: [],
//       contentTitle: [
//         { names: '行业资讯',id:'1'},
//         { names: '行业动态',id:'2'},  
//         { names: '监管政策',id:'3'},
//         { names: '最新活动',id:'4'},
//       ],
//       numberList:1
//     }
// }
state = {
  nowTab: '',
  listitems: [],
  contentTitle: [
    { names: '行业资讯',id:'1'},
    { names: '行业动态',id:'2'},  
    { names: '监管政策',id:'3'},
    { names: '最新活动',id:'4'},
  ],
  numberList:1
}
  corresponding(value) {
  }
  newlistclick(newslistthis) {
    // console.log('111')
  }
  componentDidMount() {
    // console.log(decodeURI(this.props.location.search.split('?act=')[1]))
    let idlist = decodeURI(this.props.location.search.split('?news=')[1])
    let that = this
    Axios.get(Http.listNEW, {
      params: {
        id: idlist
      }
    }).then(function (response) {
      // console.log(response.data.data)
      var ResPose = response.data
      if (ResPose && ResPose.data) {
          ResPose.data.create_time = formatDate(ResPose.data.create_time * 1000, "ymd")
        that.setState({
          listitems: ResPose.data,
        })
      }
    }).catch(function (error) {

    });
  }
  render() {
    return (
      <div className="news">
        <div style={{
          background: `url('${Topimg}') center top / cover no-repeat`,
          height: 300
        }}>
        </div>
        {/* 点击事件 */}
        <div className={appStyle.newsclick}>
          <ul>
            {
              this.state.contentTitle.map((item,index) => {
                return <li className={appStyle.About_Us} key={index}>
                    <Link to={"/news?index="+item.id}  
                    className={(item.id===this.state.listitems.journalism_label) ? appStyle.uscolorsf : appStyle.About_Usss} >{item.names}
                    </Link></li>
              })
            }
          </ul>
          <div className={appStyle.Bottom_list}>
            {/* 1 */}
            {
              <div className={appStyle.BottomListcont}>
                {/* 222 */}
                <div className={appStyle.introList}>
                  <p className={appStyle.a_cloud}>{this.state.listitems.journalism_title}</p>
                  <p className={appStyle.Times_2018}>{this.state.listitems.create_time}</p>
                  <div className={appStyle.EnterMonth}
                    dangerouslySetInnerHTML={ {__html:(this.state.listitems.content)} }
                  >
                  </div>
                </div>
              </div>
            }
          </div>
        </div>
      </div>
    );
  }
}

export default News;