export function formatDate(time,param,seperator) {  // 格式化时间
    var seperator1 = "-";
    var seperator2 = ":";
    var seperator3 = '+';
    var date = new Date(time)
    var y = date.getFullYear();
    var m = date.getMonth() + 1;
    var d = date.getDate();
    var h = date.getHours();
    var i = date.getMinutes();
    var s = date.getSeconds();
    var currentdate ;
    if (m >= 1 && m <= 9) {
        m = "0" + m;
    }
    if (d >= 0 && d <= 9) {
        d = "0" + d;
    }
    if (param === 'ymd') {
        currentdate = y + seperator1 + m + seperator1 + d;
    } else {
        currentdate = y + seperator1 + m + seperator1 + d
            + seperator3 + h + seperator2 + i
            + seperator2 + s;
    }
    return currentdate;
}
