import React, { Component } from 'react';
import appStyle from './m.module.css';
import Axios from 'axios'
import Http from '../../http/http'

class Mobile extends Component {
//   constructor(props){
//     super(props)
//     this.state = {
//       showDownLoad: false,    // 是否显示下载选项
//       wechatCode:'',
//       appCode:''
//     }
// }
state = {
  showDownLoad: false,    // 是否显示下载选项
  wechatCode:'',
  appCode:''
}
  componentDidMount() {
    this.getCode()
  }
  showDownApp() {   // 控制显示与隐藏
    this.setState({
      showDownLoad: !this.state.showDownLoad
    })
  }
  getCode() {  // 获取二维码接口
    Axios.get(Http.code)
      .then(res => {
        // console.log(res, "二维码")
        let v = res.data;
        if (v && v.data) {
          // console.log(v.data.imges1)
          this.setState({
            wechatCode: v.data.imges1,
            appCode: v.data.imges2
          })
        }
      })
  }
  render() {
    return (
      <div className={appStyle["zsqy-pc-conent"]} >
        <div className={appStyle["zsqy-index-middle"]}>
          <div>
            <div className={appStyle["ldDownloadApp"]}>
              <div className={appStyle["ldDownloadApp-main"]}>
                <div className={appStyle["ldDownloadApp-font"]}>
                  <h3>中盛惠普</h3>
                  <p className={appStyle["ldDownloadApp-font-P"]}>您身边的借款服务专家，值得拥有</p>
                  <div className={appStyle["down-icon-img"]}>
                    <img src={require('../../assets/images/advance@2x.png')} alt="优点" />
                  </div>
                  <div className={appStyle["ld-app-down-entrance"]}>
                    <a className={appStyle["down-icon"]} href="javascript:;">
                      <img
                        onMouseOver={ev => this.showDownApp(ev)}
                        onMouseOut={ev => this.showDownApp(ev)}
                        src={require("../../assets/images/btn_download@3x.png")} alt="下载APP" />
                      <div className={appStyle["down-icon-erwm"]}
                        style={this.state.showDownLoad ? style.showDownLoad : { display: 'none' }}
                      >
                        <p>推荐您到微信公众号或下载APP继续申请借款服务<br />申请额度更高，撮合效率更快哟！</p>
                        <div className={appStyle["ldDownloadApp-erw-img"]} >
                          <div className={appStyle["erw-img"]}>
                            <img src={ this.state.wechatCode } alt="中盛普惠微信公众号" />
                            <p>微信公众号</p>
                            <i></i>
                          </div>
                          <div className={appStyle["erw-img"]}>
                            <img src={ this.state.appCode } alt="中盛普惠APP下载" />
                            <p>下载APP</p>
                          </div>
                        </div>
                      </div>
                    </a>
                  </div>
                </div>
                <img className={appStyle["phone-group-img"]} src={require("../../assets/images/phone_group@2x.png")} alt="下载中盛普惠APP" />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const style = {
  showDownLoad: {
    display: 'block'
  }
}

export default Mobile;